@import "./mixins";
$block-size: 30vh;
#flip-book {
  height: 150vh;
  .content {
    width: 100%;
    height: 100vh;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    .grid {
      padding: 0;
      margin: 0 auto;
      list-style: none;
      display: flex;
      justify-content: center;
      width: 100vh;
      flex-wrap: wrap;
      .item {
        flex: $block-size 1 auto;
        width: $block-size;
        padding: 5px;
        margin: 5px;
        position: relative;
        perspective: 1000px;
        transition: filter 0.5s;
        filter: grayscale(100%);
        &:before {
          content: '';
          float: left;
          padding-top: 100%;
        }
        &:hover {
          filter: grayscale(0%);
        }
        .flip-box {
          transition: transform 0.1s;
          transform-style: preserve-3d;
          height: 100%;
        }
        .slide {
          border: 1px solid rgba(200, 200, 200, .4);
          background: white;
          display: flex;
          flex: 1 1 auto;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          width: $block-size;
          height: $block-size;
          backface-visibility: hidden;
          position: absolute;
          &.back {
            transform: rotateX(180deg);
          }
          &.circle {
            border-radius: 50%;
          }
          &.black {
            background: black;
            color: white;
          }
          &.maker {
            h4 {
              font-weight: 700;
              text-transform: uppercase;
              font-size: 1.75rem;
              // margin: 0;
              @include my_bp(md) {
                font-size: 1.5rem
              }
            }
          }
          &:hover {
            .hover {
              opacity: 1;
              height: 100% !important;
              top: 0;
              &.half {
                bottom: 0 !important;
                height: 30% !important;
                top: auto;
              }
            }
          }
          .hover {
            position: absolute;
            padding: 10px;
            background: rgba(0, 0, 0, .75);
            transition: all 0.4s ease-in;
            // transition-delay: .2s;
            color: white;
            text-align: justify;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            height: 0px;
            width: 100%;
            opacity: 0;
            top: 50%;
            &.half {
              bottom: 0;
              top: auto;
              padding-bottom: 5px;
            }
            h4 {
              font-weight: 700;
              text-transform: capitalize;
              font-size: 1.2rem;
              text-align: center;
            }
            p {
              text-align: justify;
              font-size: .9rem;
              line-height: 1.2rem;
            }
          }
        }
      }
    }
  }
}